body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.nav{
    background-color: rgb(6, 6, 78);
    height: 100vh;
    color: white;
    display: inline-block;
    position: fixed;
    min-width: 150px;
}

.nav a{
    color: white;
    display: block;
    text-align: center;
    border: 1px solid white;
    margin: 1em;
    text-decoration: none;
    padding: 1em;
    border-radius: 5px;
    -webkit-transition: 0.2s ease all;
    transition: 0.2s ease all;
}
.nav a:hover{
    color: grey;
}

.page-container{
    padding: 1em;
    display: inline-block;
    background-color: white;
    margin-left: 150px;
    max-width: 1200px;
    width: 72%;
}

.is-active{
    color: rgb(6, 6, 78) !important;
    background-color: white;
}

.courseItem{
    border: 1px solid rgb(6, 6, 78);
    margin: 1em 0;
    padding: 1em;
    border-radius: 5px;
    position: relative;
}

.languageDisplay{
    position: absolute;
    right: 2%;
    top: 4%;
    
}
.languageDisplay p{
    margin: 0;
}
h1{
    margin-top: 0;
}

.courseNameLink{
    color:  rgb(6, 6, 78);
}

.courseFlex{
    display: flex;
    justify-content: space-between;
}
.courseFlex div{
    width: 50%;
}
.bold{
    font-weight: bold;
}

.dashboard-icons{
    border: 1px solid black;
    width: 100px;
    height: 100px;
    text-align: center;
}

.icons-container{
    display: flex;
}
form {
  display: flex;
  flex-direction: column;
  width: 50%;
}

select,
input,
textarea,
button,
select {
  margin: 0.2em 0;
  border: 1px solid #cccccc;
  border-radius: 5px;
  padding: 1em;

  color: #333;
  font-size: 20px;
  font-weight: bold;
}

select {
  background-color: #ffffff;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  position: relative;
}

select::after{
    content: '>'; 
}



button {
  background-color: rgb(6, 6, 78);
  font-size: 1em;
  color: white;
}


.school-list{
    width: 50%;
    padding: 0;
}

.school-list li{
    width: 100%;
    list-style: none;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    border: 1px solid black;
    border-radius: 5px;
    padding-left: .5em;
}
.school-list p{
    font-size: 1.5em;
}

